router.afterEach((to, from) => {
  if (to.name.indexOf('加入小着') > -1) {
    document.querySelector('title').innerHTML = '加入小着'
  } else if (to.name.indexOf('会员入驻') > -1) {
    document.querySelector('title').innerHTML = '会员入驻'
  } else if (to.name.indexOf('好友请你到小着免费喝果汁') > -1) {
    document.querySelector('title').innerHTML = '好友请你到小着免费喝果汁'
  } else {
    document.querySelector('title').innerHTML = to.name
  }
})
export default router
