import Vue from 'vue'
import HiDialog from './Dialog.vue'

let instance

const DialogConstructor = Vue.extend(HiDialog)

let defaultOptions = {
  title: '',
  message: '',
  theme: 'light',
  cancelButtonText: '取消',
  confirmButtonText: '确认',
  showCancelButton: false,
  showCenterButton: false,
  showConfirmButton: true,
  visible: true,
  callback: action => {
    instance[action === 'confirm' ? 'resolve' : 'reject'](action)
  }
}

DialogConstructor.prototype.close = () => {
  if (instance) {
    instance.visible = false
  }
}

function initInstance () {
  if (instance) {
    instance.$destroy()
  }

  instance = new DialogConstructor().$mount(document.createElement('div'))
}

function Dialog (options) {
  return new Promise((resolve, reject) => {
    if (!instance || !document.body.contains(instance.$el)) {
      initInstance()
    }
    document.body.appendChild(instance.$el)
    instance = Object.assign(instance, defaultOptions, options, {
      resolve,
      reject
    })
  })
}

Dialog.install = () => {
  Vue.use(HiDialog)
}

Dialog.Component = HiDialog

Vue.prototype.$dialog = Dialog
Vue.dialog = Dialog

export default Dialog
