<template>
  <Overlay
    :visible.sync="innerVisible"
    :class="wrapperClassName">
    <transition name="dialog-fade">
      <div
        v-if="visible"
        :class="['higo-dialog', theme, className]">
        <div
          v-if="title"
          class="higo-dialog-header">
          {{ title }}
        </div>
        <div
          class="higo-dialog-message"
          v-if="$slots.default">
          <slot></slot>
        </div>
        <div
          class="higo-dialog-message"
          v-else-if="message"
          v-text="message"></div>
        <div :class="['higo-dialog-footer', showCancelButton && showCenterButton && showConfirmButton ? 'small-text' : '' ]">
          <button
            v-if="showCancelButton"
            class="higo-button higo-dialog_cancel-button"
            @click="handleClick('cancel')">
            {{ cancelButtonText || '取消' }}
          </button>
          <button
            v-if="showCenterButton"
            class="higo-button higo-dialog_cancel-button"
            @click="handleClick('center')">
            {{ centerButtonText }}
          </button>
          <button
            v-if="showConfirmButton"
            class="higo-button higo-dialog_confirm-button"
            @click="handleClick('confirm')">
            {{ confirmButtonText || '确认' }}
          </button>
        </div>
      </div>
    </transition>
  </Overlay>
</template>

<script>
import Overlay from '../overlay'
export default {
  name: 'Dialog',
  components: { Overlay },
  props: {
    title: String,
    message: String,
    theme: {
      type: String,
      default: 'light'
    },
    wrapperClassName: String,
    className: String,
    confirmButtonText: String,
    cancelButtonText: String,
    centerButtonText: String,
    showCancelButton: Boolean,
    showConfirmButton: {
      type: Boolean,
      default: true
    },
    showCenterButton: {
      type: Boolean,
      default: false
    },
    visible: Boolean,
    callback: Function
  },
  data () {
    return {
      innerVisible: false
    }
  },
  watch: {
    visible (val) {
      this.innerVisible = val
    },
    innerVisible (val) {
      this.visible = val
      this.$emit('update:visible', val)
      if (!val) {
        // this.handleClick('cancel')
      }
    }
  },
  methods: {
    handleClick (action) {
      this.$emit(action)
      if (this.close) {
        this.close()
        if (this.callback) {
          this.callback(action)
        }
      }
    }
  }
}
</script>

