    }
  },
  created () {
  },
  mounted () {
    open.ready(() => {
      this.isReady = true
    })
    // 防止ios可能存在第一次cookie没存进webview，刷新一次
    // number 防止死循环
    // alert(_utils.getCookie('access_token'))
    // if (!_utils.getCookie('access_token') && (!localStorage.getItem('number') || localStorage.getItem('number') < 3)) {
    //   localStorage.setItem('number', localStorage.getItem('number') ? Number(localStorage.getItem('number')) + 1 : 1)
    //   history.go(0)
    //   // location.reload()
    //   return false
    // }
    // if (!_utils.getCookie('access_token') && Number(localStorage.getItem('number')) === 3) {
