import Vue from 'vue'
import App from './App.vue'
import store from './vuex'
import router from './router'

import normalTitle from 'higgs-ui/lib/normalTitle'
import mixCountdown from 'higgs-ui/lib/mixCountdown'
import lineDivider from 'higgs-ui/lib/lineDivider'
import normalPic from 'higgs-ui/lib/normalPic'
import mixCombination from 'higgs-ui/lib/mixCombination.js'
import mixImgText from 'higgs-ui/lib/mixImgText.js'
import imageBanner from 'higgs-ui/lib/imageBanner.js'
import imageTextBanner from 'higgs-ui/lib/imageTextBanner.js'
import anchor from 'higgs-ui/lib/anchor'
import hotmap from 'higgs-ui/lib/hotmap'
import normalAudio from 'higgs-ui/lib/normalAudio'
import normalVideo from 'higgs-ui/lib/normalVideo'
import normalText from 'higgs-ui/lib/normalText'
import labelText from 'higgs-ui/lib/labelText'
import richText from 'higgs-ui/lib/richText'
import xzGoodsList from 'higgs-ui/lib/xzGoodsList'
import qiniuVideo from 'higgs-ui/lib/qiniuVideo'
import attention from 'higgs-ui/lib/attention'

import 'css-reset'
// import './mock'
import VueClipboard from 'vue-clipboard2'
import * as filters from './filters' // 全局过滤器

import Toast from '@/components/toast'
import Dialog from '@/components/dialog/api'
import Loading from '@/components/loading'
import '@/style/public.css'
import 'higgs-ui/lib/higgs-ui.css'
import VueLazyload from 'vue-lazyload'
import * as Sentry from '@sentry/browser'
import * as Integrations from '@sentry/integrations'
import { DatetimePicker, Popup, Area } from 'vant'

import VueLocalForage from 'vue-localforage'

Vue.config.productionTip = false
Vue.use(normalTitle)
Vue.use(mixCountdown)
Vue.use(lineDivider)
Vue.use(normalPic)
Vue.use(mixCombination)
Vue.use(mixImgText)
Vue.use(imageBanner)
Vue.use(imageTextBanner)
Vue.use(anchor)
Vue.use(hotmap)
Vue.use(normalAudio)
Vue.use(normalVideo)
Vue.use(normalText)
Vue.use(labelText)
Vue.use(xzGoodsList)
Vue.use(richText)
Vue.use(qiniuVideo)
Vue.use(attention)

Vue.use(Toast)
Vue.use(Dialog)
Vue.use(Loading)
Vue.use(Area)
Vue.use(VueClipboard)
Vue.use(VueLazyload, {
  error: '',
  loading: '',
  preLoad: 1,
  attempt: 3
})
Vue.use(DatetimePicker)
Vue.use(Popup)
Vue.use(VueLocalForage)

Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})

if (process.env.NODE_ENV === 'production') {
  Sentry.init({
    dsn: 'https://1@www.lehe.com/sentry/xz',
    integrations: [new Integrations.Vue({ Vue, attachProps: true })]
  })
}

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')
