<template>
  <transition name="fade">
    <div
      class="higo-loading"
      v-show="visible">
      <img
        src="@/assets/icon_loading.png"
        alt="">
    </div>
  </transition>
</template>
<script>
import { duration } from './constant.js'
export default {
  name: 'HigoLoading',
  props: {

  },
  mixins: [ ],
  data () {
    return {
      duration,
      visible: false
    }
  },
  created () {

  },
  mounted () {
    this.createTimer()
  },
  destroyed () {
    this.clearTimer()
  },
  methods: {
    clearTimer () {
      clearTimeout(this.timer)
    },
    createTimer () {
      this.timer = setTimeout(() => {
        this.visible = false
      }, this.duration)
    },
    close () {
      clearTimeout(this.timer)
    }
  }
}
</script>
