import Vue from 'vue'
import LoadingComponent from './main.vue'
import { duration } from './constant.js'

const LoadingConstructor = Vue.extend(LoadingComponent)
let instance

LoadingConstructor.prototype.close = _ => {
  if (instance) {
    instance.visible = false
  }
}
const initInstance = _ => {
  instance = new LoadingConstructor().$mount(document.createElement('div'))
}

const Loading = (options) => {
  // 共用一个实例
  if (!instance) {
    initInstance()
  }
  instance.duration = options && options.duration ? options.duration : duration
  instance.clearTimer()
  instance.createTimer()
  document.body.appendChild(instance.$el)
  instance.visible = true

  return instance
}

Loading.close = _ => {
  instance.visible = false
}

export default Loading
