
<script>
// <template>
//   <div @click="handleClose">
//     <transition name="overlay-fade">
//       <div
//         v-if="visible"
//         v-fixed
//         class="higo-overlay">
//       </div>
//     </transition>
//     <slot></slot>
//   </div>
// </template>
const overflowScrollReg = /scroll|auto/i
const MIN_DISTANCE = 1

export default {
  name: 'HiOverlay',
  render () {
    const inner = <div onClick={this.handleClose} class="higo-overlay"/>
    return (
      <div>
        <transition name="overlay-fade">
          {this.visible ? inner : '' }
        </transition>
        {this.$slots.default}
      </div>
    )
  },
  props: {
    visible: Boolean
  },
  directives: {
    fixed: {
      // inserted 被绑定元素插入父节点时调用
      inserted () {
        // document.addEventListener('touchstart', this.touchStart, { capture: false, passive: false })
      //  document.addEventListener('touchmove', this.onTouchMove, { capture: false, passive: false })
        document.body.classList.add('higo-overflow-hidden')
        // let scrollTop = document.body.scrollTop || document.documentElement.scrollTop
        // document.body.style.cssText += 'position:fixed;width:100%;top:-' + scrollTop + 'px;'
      },
      // unbind 指令与元素解绑时调用
      unbind () {
        // document.removeEventListener('touchstart', this.touchStart, { capture: false, passive: false })
        // document.removeEventListener('touchmove', this.onTouchMove, { capture: false, passive: false })
        document.body.classList.remove('higo-overflow-hidden')
        // let body = document.body
        // body.style.position = ''
        // let top = body.style.top
        // document.body.scrollTop = document.documentElement.scrollTop = -parseInt(top)
        // body.style.top = ''
      }
    }
  },
  data () {
    return {
      startX: 0,
      startY: 0,
      deltaX: 0,
      deltaY: 0,
      offsetX: 0,
      offsetY: 0,
      direction: ''
    }
  },
  watch: {
    visible (val) {
      if (val) {
        document.addEventListener('touchstart', this.touchStart, { capture: false, passive: false })
        document.addEventListener('touchmove', this.onTouchMove, { capture: false, passive: false })
      } else {
        document.removeEventListener('touchstart', this.touchStart, { capture: false, passive: false })
        document.removeEventListener('touchmove', this.onTouchMove, { capture: false, passive: false })
      }
    }
  },
  mounted () {
    // document.addEventListener('touchstart', this.touchStart, { capture: false, passive: false })
    // document.addEventListener('touchmove', this.onTouchMove, { capture: false, passive: false })
  },
  beforeDestroy () {
    // document.removeEventListener('touchstart', this.touchStart, { capture: false, passive: false })
    // document.removeEventListener('touchmove', this.onTouchMove, { capture: false, passive: false })
  },
  methods: {
    getDirection (x, y) {
      if (x > y && x > MIN_DISTANCE) {
        return 'horizontal'
      }

      if (y > x && y > MIN_DISTANCE) {
        return 'vertical'
      }

      return ''
    },

    touchMove (event) {
      console.log(event, 'touchMove')
      const touch = event.touches[0]
      this.deltaX = touch.clientX - this.startX
      this.deltaY = touch.clientY - this.startY
      this.offsetX = Math.abs(this.deltaX)
      this.offsetY = Math.abs(this.deltaY)
      this.direction = this.direction || this.getDirection(this.offsetX, this.offsetY)
    },

    onTouchMove (event) {
      this.touchMove(event)
      const direction = this.deltaY > 0 ? '10' : '01'
      const el = this.getScrollEventTarget(event.target, this.$el)
      const { scrollHeight, offsetHeight, scrollTop } = el
      let status = '11'

      /* istanbul ignore next */
      if (scrollTop === 0) {
        status = offsetHeight >= scrollHeight ? '00' : '01'
      } else if (scrollTop + offsetHeight >= scrollHeight) {
        status = '10'
      }

      /* istanbul ignore next */
      if (
        status !== '11' &&
        this.direction === 'vertical' &&
        !(parseInt(status, 2) & parseInt(direction, 2))
      ) {
        this.preventDefault(event, true)
      }
    },
    preventDefault (event, isStopPropagation) {
      /* istanbul ignore else */
      if (typeof event.cancelable !== 'boolean' || event.cancelable) {
        event.preventDefault()
      }

      if (isStopPropagation) {
        this.stopPropagation(event)
      }
    },
    stopPropagation (event) {
      event.stopPropagation()
    },
    touchStart (event) {
      // console.log(event, 'touchStart')
      this.resetTouchStatus()
      this.startX = event.touches[0].clientX
      this.startY = event.touches[0].clientY
    },
    resetTouchStatus () {
      this.direction = ''
      this.deltaX = 0
      this.deltaY = 0
      this.offsetX = 0
      this.offsetY = 0
    },
    getScrollEventTarget (element, rootParent = window) {
      let node = element
      while (
        node &&
    node.tagName !== 'HTML' &&
    node.nodeType === 1 &&
    node !== rootParent
      ) {
        const { overflowY } = window.getComputedStyle(node)
        if (overflowScrollReg.test(overflowY)) {
          if (node.tagName !== 'BODY') {
            return node
          }

          // see: https://github.com/youzan/vant/issues/3823
          const { overflowY: htmlOverflowY } = window.getComputedStyle(node.parentNode)
          if (overflowScrollReg.test(htmlOverflowY)) {
            return node
          }
        }
        node = node.parentNode
      }
      return rootParent
    },
    handleTouchMove (e) {
      e.preventDefault()
    },
    handleClose () {
      // this.visible = false
      this.$emit('update:visible', false)
      // this.$emit('test', false)
    }
  }
}
</script>
<style lang="scss" scoped>
@import "../../style/index.scss";
.higo-overlay {
  z-index:999;
  // height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.6);
}
.overlay-fade-enter-active {
  animation: overlay-fade-in .3s;
}
.overlay-fade-leave-active {
  animation: overlay-fade-out .3s;
}
